import dnIcon from '@/components/dn-icon/dn-icon.vue'
import ccPopupList from "@/components/m-cc-popup/m-cc-popup.vue"

export default {
	components: {
		dnIcon,
		ccPopupList
	},
	data() {
		return {
			title: 'radio 单选框',
			items: [{
					value: '1',
					name: '微信支付',
					note: '推荐只用微信支付',
					icon: 'weixin',
					color: 'rgb(54, 201, 88)'
				},
				{
					value: '2',
					name: '支付宝支付',
					checked: 'true',
					note: '',
					icon: 'umidd17',
					color: 'rgb(1, 170, 239)'
				},
				{
					value: '3',
					name: '工商银行卡',
					note: '银行卡号：8498******1000',
					icon: 'renminbi',
					color: 'rgb(253, 142, 46)'
				},
			],
			current: 0,
			cc: false
		}
	},
	methods: {
		radioChange(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		},
		// 打开popup
		async showPopup() {
			console.log('asd')
			this.$refs.ccPopupList.show();
		},
		// popup选中结果
		async ccSelectResult(card) {
			console.log(card)
			this.cc = card;
		},
		// 确认支付
		handleConfirmPayment(url) {
			uni.showLoading({
				title: '正在提交...',
				mask: true,
				success() {
					setTimeout(() => {
						uni.hideLoading()
						uni.showToast({
							icon: "success",
							title: '提交成功',
							duration: 1000
						});
						setTimeout(() => {
							uni.navigateTo({
								url: url,
								success: res => {},
								fail: () => {},
								complete: () => {}
							})
						}, 1000)
					}, 1500)
				}
			})
		}
	}
}
